:root {
    --main-bg: #181d27;
    --card-bg: #232837;
    --main-blue: #4cc9f0;
    --main-blue-hover: #2196f3;
    --border: #313b4f;
    --label: #a2aabc;
    --shadow: 0 4px 18px 0 rgba(30,60,120,0.11);
    --shadow-hover: 0 8px 22px 0 rgba(30,60,120,0.16);
    --radius: 22px;
    --input-bg: #232838;
    --input-text: #f4f6fa;
    --result-bg: #21263c;
    --result-label: #98a6c7;
    --result-value: #4cc9f0;
}

.calculator {
    font-family: 'Roboto', sans-serif;
    max-width: 400px;
    margin: 0 auto;
    padding: 15px 8px;
    line-height: 1.4;
    background: var(--card-bg);
    box-shadow: var(--shadow);
    border-radius: var(--radius);
    color: var(--input-text);
}

.calculator h1 {
    color: var(--main-blue);
    text-align: center;
    font-size: 20px;
    margin-bottom: 15px;
    font-weight: 700;
    letter-spacing: 0.02em;
}

.calculator h2 {
    color: var(--main-blue);
    font-size: 16px;
    margin: 12px 0 8px;
    font-weight: 700;
}

.calculator h3 {
    color: #2c3e50;
    font-size: 14px;
    margin: 10px 0 6px;
    font-weight: 500;
}

.input-group {
    margin-bottom: 8px;
    background: var(--input-bg);
    padding: 8px;
    border-radius: 11px;
    transition: all 0.3s ease;
}

.input-group:hover {
    background: #283042;
}

.input-group label {
    display: block;
    margin-bottom: 4px;
    font-weight: 500;
    color: var(--label);
    font-size: 12px;
    letter-spacing: 0.01em;
}

.input-group input,
.input-group select {
    width: 100%;
    padding: 6px;
    border: 1.5px solid var(--border);
    border-radius: 11px;
    font-size: 12px;
    transition: all 0.3s ease;
    background: var(--input-bg);
    color: var(--input-text);
    outline: none;
}

.input-group input:focus,
.input-group select:focus {
    border-color: var(--main-blue);
    background: #232e41;
}

.calculator button {
    background: linear-gradient(135deg, var(--main-blue) 0%, var(--main-blue-hover) 100%);
    color: #181d27;
    border: none;
    padding: 8px 15px;
    border-radius: 11px;
    cursor: pointer;
    font-size: 14px;
    width: 100%;
    margin: 8px 0;
    transition: all 0.3s ease;
    font-weight: 600;
}

.calculator button:hover {
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(76, 201, 240, 0.3);
}

.results {
    margin-top: 15px;
    background: var(--result-bg);
    border-radius: 15px;
    padding: 10px;
    box-shadow: 0 1px 7px rgba(76,201,240,0.03);
}

.result-item {
    margin-bottom: 8px;
    padding: 8px;
    background: var(--card-bg);
    border-radius: 11px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
}

.result-item:hover {
    transform: translateY(-1px);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.result-item strong {
    color: var(--result-label);
    font-size: 12px;
    display: block;
    margin-bottom: 3px;
    font-weight: 500;
}

.result-item div {
    color: var(--result-value);
    font-size: 14px;
    font-weight: 700;
}

.monthly-results {
    margin-top: 30px;
}

.scrollable-table {
    max-height: 500px;
    overflow-y: auto;
    margin: 20px 0;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    background: #fff;
}

th, td {
    padding: 15px;
    text-align: left;
    border-bottom: 1px solid #e9ecef;
}

th {
    background: #f8f9fa;
    color: #495057;
    font-weight: 500;
    position: sticky;
    top: 0;
    z-index: 1;
}

tr:last-child td {
    border-bottom: none;
}

tr:hover td {
    background: #f8f9fa;
}

.summary {
    background: #e7f5ff !important;
    font-weight: 500;
}

.summary td {
    padding: 20px 15px;
}

@media (max-width: 768px) {
    .calculator {
        padding: 8px 6px;
    }

    .calculator h1 {
        font-size: 18px;
    }

    .input-group {
        padding: 6px;
    }

    .result-item {
        padding: 6px;
    }

    th, td {
        padding: 10px;
        font-size: 14px;
    }
} 